#testimonials {
  background: white;

  .container {
    margin-left: 0;
    margin-right: 0;
  }
  
  header {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    margin-bottom: 0;
  }
  
  blockquote {
    padding: 2rem;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.08);
    border-radius: 0.25rem;

    p {
      position: relative;
      text-indent: 1.875rem;
      margin-bottom: 1.5rem;
      color: var(--title-color);

      span {
        font: 700 2.5rem serif;
        position: absolute;
        top: -0.375rem;
        left: -1.875rem;
        color: var(--base-color);
      }
    }
  }
  
  cite {
    display: flex;
    align-items: center;
    font-style: normal;

    img {
      width: 2rem;
      height: 2rem;
      object-fit: cover;
      clip-path: circle();
      margin-right: 0.5rem;
    }  
  }
}

/* -----------------------------------------------------------------------------
  Swiper
----------------------------------------------------------------------------- */
.swiper-slide {
  height: auto;
  padding: 4rem 1rem;
}

.swiper-pagination-bullet {
  width: 0.75rem;
  height: 0.75rem;
}

.swiper-pagination-bullet-active {
  background: var(--base-color);
}